<h2 id="什么是单链表" tabindex="-1"><a class="header-anchor" href="#什么是单链表" aria-hidden="true">#</a> 什么是单链表？</h2>
<p>链表，别名链式存储结构或单链表，用于存储逻辑关系为 &quot;一对一&quot; 的数据。与顺序表不同，链表不限制数据的物理存储状态，换句话说，使用链表存储的数据元素，其物理存储位置是随机的。</p>
